<template>
  <div class="container">
    <div class="card card-custom gutter-b">
      <div class="card-body">
        <h4 class="card-title mb-0 font-weight-bolder mb-3">{{ config.title }}</h4>
        <!-- <hr class="my-3"> -->
        <global-filter query inline @change="globalFilterChange($event)" />
        <template v-if="isValidFilter">
          <hr style="border-style: dashed">
          <div class="d-flex align-items-center justify-content-start">
            <div class="d-flex flex-column align-items-start">
              <span class="d-block font-weight-bold font-size-lg mb-2" for="">Pilih Agenda :</span>
              <v-select :clearable="false" v-model="selectedAgenda" :appendToBody="true" class="vs-style custom min-w-250px" :options="agendaList" :placeholder="'Pilih salah satu Agenda'">
                <slot name="no-options">{{ $t('error.no_data') }}</slot>
              </v-select>
              <div class="mt-1">
                <button id="add-newAgenda" class="btn btn-primary btn-sm"><i class="ri-add-line ri-2x"></i> Tambah Agenda Baru</button>
                <button @click="newAgenda=selectedAgenda.label" :disabled="!selectedAgenda.code" :class="!selectedAgenda.code? 'btn-mute': 'btn-outline-warning'" id="update-newAgenda" class="btn btn-sm btn-icon"><i class="ri-edit-line ri-2x"></i></button>
                <button :disabled="!selectedAgenda.code" @click="deleteAgenda()" :class="!selectedAgenda.code? 'btn-mute': 'btn-outline-danger'" class="btn btn-sm btn-icon"><i class="ri-delete-bin-line ri-2x"></i></button>
              </div>
              <b-popover target="add-newAgenda" title="Tambah Agenda Baru (Stress Test)" triggers="click blur" placement="right" custom-class="popover-xl">
                <form autocomplete="off" @submit.prevent="addNewAgenda()">
                  <s-input v-model="newAgenda" :item="{ label:'Nama Stress Test', type:'text', validation:[], placeholder: 'contoh: Stress Test Triwulan 1', api:'' }" />
                  <button class="btn btn-primary btn-block">Simpan</button>
                </form>
              </b-popover>
              <b-popover @hide="newAgenda=null" target="update-newAgenda" title="Perbarui Agenda" triggers="click blur" placement="right" custom-class="popover-xl">
                <form autocomplete="off" @submit.prevent="updateAgenda()">
                  <s-input v-model="newAgenda" :item="{ label:'Nama Stress Test', type:'text', validation:[], placeholder: 'contoh: Stress Test Triwulan 1', api:'' }" />
                  <button class="btn btn-primary btn-block">Perbarui</button>
                </form>
              </b-popover>
            </div>
            <div class="d-flex flex-column align-items-start ml-5">
              <span class="d-block font-weight-bold font-size-lg mb-2" for="">Batas Pengumpulan :</span>
              <s-input v-model="cutOff.closing_date" :item="{ label:'', attr: [{clearable: false}], prefix:'', nm:true, inline:false, type:'datetime', validation:['required'] }" />
              <button @click="updateCutoff()" class="btn btn-primary mt-1 btn-sm btn-block border-0"><i class="ri-save-line ri-2x"></i>Perbarui Cut Off</button>
            </div>
          </div>
        </template>
      </div>
    </div>
    <div v-if="isValidFilter" class="position-relative">
      <b-overlay :show="isLoading" no-wrap rounded="sm" />
      <div class="row">
        <div class="col-lg-4">
          <div class="card card-custom gutter-b">
            <div class="card-header border-0 pt-7" style="min-height:0 !important">
              <div class="input-icon flex-fill mr-3">
                <input v-model.lazy.trim="searchFilter" autocomplete="off" name="search" type="text" class="form-control seach-form" :placeholder="'Cari item Template RKAP Kualitatif'">
                <span>
                  <i class="ri-search-line text-primary"></i>
                </span>
              </div>
              <div class="card-toolbar my-0">
                <button class="btn btn-primary btn-icon" id="add-newItem">
                  <i class="ri-add-line"></i>
                </button>
                <b-popover target="add-newItem" title="Tambah Template RKAP Kualitatif" triggers="click blur" placement="right" custom-class="popover-xl">
                  <form autocomplete="off" @submit.prevent="addNewItems()">
                    <s-input v-model="newItem" :item="{ label:'Nama Template', type:'text', validation:[], placeholder: 'contoh: Template Cashflow', api:'' }" />
                    <button class="btn btn-primary btn-block">Simpan</button>
                  </form>
                </b-popover>
              </div>
            </div>
            <div class="card-body pt-0">
              <div v-if="$route.query.id" class="alert alert-danger mb-0 mt-2 p-2 d-flex align-items-center" role="alert">
                <i class="ri-shield-flash-line text-white pr-2 ri-2x"></i>
                <div>
                  <span>Hanya menampilkan template dari <b>My Action</b>!</span>
                  <span @click="removeMyAction()" class="d-block text-white font-weight-bolder pointer pr-2 text-underline">Tampilkan semua</span>
                </div>
              </div>
              <template v-if="dataList.data.length">
                <table class="table mt-3 mb-0">
                  <template v-for="(m, i) in dataList.data">
                    <tr :key="i+'-template-rkap'">
                      <td class="nowrap-table pl-0">
                        <span :class="activeTemplate.id === m.id? 'svg-icon-primary': 'svg-icon-light'" class="svg-icon">
                          <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                            <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                              <polygon points="0 0 24 0 24 24 0 24"></polygon>
                              <path d="M12.2928955,6.70710318 C11.9023712,6.31657888 11.9023712,5.68341391 12.2928955,5.29288961 C12.6834198,4.90236532 13.3165848,4.90236532 13.7071091,5.29288961 L19.7071091,11.2928896 C20.085688,11.6714686 20.0989336,12.281055 19.7371564,12.675721 L14.2371564,18.675721 C13.863964,19.08284 13.2313966,19.1103429 12.8242777,18.7371505 C12.4171587,18.3639581 12.3896557,17.7313908 12.7628481,17.3242718 L17.6158645,12.0300721 L12.2928955,6.70710318 Z" fill="#000000" fill-rule="nonzero"></path>
                              <path d="M3.70710678,15.7071068 C3.31658249,16.0976311 2.68341751,16.0976311 2.29289322,15.7071068 C1.90236893,15.3165825 1.90236893,14.6834175 2.29289322,14.2928932 L8.29289322,8.29289322 C8.67147216,7.91431428 9.28105859,7.90106866 9.67572463,8.26284586 L15.6757246,13.7628459 C16.0828436,14.1360383 16.1103465,14.7686056 15.7371541,15.1757246 C15.3639617,15.5828436 14.7313944,15.6103465 14.3242754,15.2371541 L9.03007575,10.3841378 L3.70710678,15.7071068 Z" fill="#000000" fill-rule="nonzero" opacity="0.3" transform="translate(9.000003, 11.999999) rotate(-270.000000) translate(-9.000003, -11.999999)"></path>
                            </g>
                          </svg>
                        </span>
                      </td>
                      <td colspan="2" class="pl-0 pointer" @click="showTemplate(m)">
                        <div :class="activeTemplate.id === m.id? 'text-primary': 'text-dark-75'" class="text-line-2 text-hover-primary mb-1 font-size-lg font-weight-bolder"> {{ m.template_name }} </div>
                        <span class="d-bloc text-dark-75 font-weight-bold font-size-xs">{{ m.number_of_template_attachment || 0 }} Panduan</span>
                      </td>
                      <td class="nowrap-table v-center text-center">
                        <i v-if="activeTemplate.status_code_data_verification !== 'open'" class="ri-lock-line ri-2x"></i>
                      </td>
                    </tr>
                  </template>
                </table>
                <hr class="mt-0">
                <div class="d-flex align-items-center justify-content-between">
                  <span class="text-muted">Total {{ dataList.total }} Item</span>
                  <b-pagination class="mb-0" v-model="usedFilter.page" :total-rows="dataList.totalPage" :per-page="usedFilter.limit" prev-text="Sebelumnya" next-text="Selanjutnya" first-number last-number />
                </div>
              </template>
              <template v-else>
                <div class="d-flex flex-column flex-fill justify-content-center">
                  <img src="/static/img/default/no-data_form.svg" alt="" class="img-fluid px-5 py-3">
                  <div class="alert alert-custom alert-light-primary mt-3" role="alert">
                    <div class="alert-icon">
                      <span class="svg-icon svg-icon-primary svg-icon-xl">
                        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                          <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                            <polygon points="0 0 24 0 24 24 0 24"></polygon>
                            <path d="M5.85714286,2 L13.7364114,2 C14.0910962,2 14.4343066,2.12568431 14.7051108,2.35473959 L19.4686994,6.3839416 C19.8056532,6.66894833 20,7.08787823 20,7.52920201 L20,20.0833333 C20,21.8738751 19.9795521,22 18.1428571,22 L5.85714286,22 C4.02044787,22 4,21.8738751 4,20.0833333 L4,3.91666667 C4,2.12612489 4.02044787,2 5.85714286,2 Z" fill="#000000" fill-rule="nonzero" opacity="0.3"></path>
                            <path d="M11,14 L9,14 C8.44771525,14 8,13.5522847 8,13 C8,12.4477153 8.44771525,12 9,12 L11,12 L11,10 C11,9.44771525 11.4477153,9 12,9 C12.5522847,9 13,9.44771525 13,10 L13,12 L15,12 C15.5522847,12 16,12.4477153 16,13 C16,13.5522847 15.5522847,14 15,14 L13,14 L13,16 C13,16.5522847 12.5522847,17 12,17 C11.4477153,17 11,16.5522847 11,16 L11,14 Z" fill="#000000"></path>
                          </g>
                        </svg>
                      </span>
                    </div>
                    <div class="alert-text">Belum ada Template Pengumpulan RKAP {{ fullFilter.division_id ? fullFilter.division_id.label : '' }} Tahun {{ fullFilter.year || '' | parse('year') }}</div>
                  </div>
                </div>
              </template>
            </div>
          </div>
        </div>
        <div class="col-lg-8">
          <template v-if="Object.keys(activeTemplate).length">
            <div class="card card-custom gutter-b">
              <div class="card-body">
                <div v-if="allowManipulateAttachment(activeTemplate)" class="d-flex">
                  <input type="text" v-model="activeTemplate.template_name" class="form-control">
                  <button @click="updateItems(activeTemplate)" class="btn btn-outline-primary ml-3">Perbarui</button>
                </div>
                <div v-else>
                  <h4>{{activeTemplate.template_name}}</h4>
                </div>
                <span class="d-block font-weight-bolder mt-4 mb-1">Daftar Panduan ({{ activeTemplate.number_of_template_attachment }}) : </span>
                <div class="d-flex flex-wrap">
                  <div v-for="(c,i) in activeTemplate.child_data_template_qualitatif_on_division_attachments" :key="i+'-child'" class="border pr-3 pl-2 py-3 mr-3 mb-3" style="border-radius:8px">
                    <div class="d-flex">
                      <img :src="'/static/img/file/'+ c.file_document.ext +'.svg'" class="pr-2" style="objec-fit:cover;width:50px">
                      <div class="d-flex flex-column justify-content-between flex-fill font-size-sm font-weight-bold">
                        <div class="d-block max-w-180px min-w-180px text-truncate">{{c.file_document.filename}}</div>
                        <div class="d-flex align-items-end mt-1">
                          <span @click="showAtachment(c)" class="pointer pr-3 text-info">Preview</span>
                          <a @click="$_sys.forceDownload(c.file_document)" class="pointer pr-3 text-info">Download </a>
                          <div v-if="allowManipulateAttachment(activeTemplate)" class="d-flex flex-fill justify-content-end align-items-end">
                            <i @click="deleteAttachment(c)" class="ri-delete-bin-2-line text-danger pointer" style="line-height:1.2"></i>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div v-if="allowManipulateAttachment(activeTemplate)" class="d-flex align-items-center">
                  <label class="mb-0">
                    <a class="btn btn-sm btn-primary">Upload Panduan Baru (max {{ maxUploadSize }}Mb)</a>
                    <input ref="inputFileKualitatif" id="inputFileKualitatif" type="file" hidden @change="handleFile($event)">
                  </label>
                  <div class="flex-fill ml-3">
                    <b-progress :max="100" animated v-if="isUploading">
                      <b-progress-bar :value="uploadPercentage" :label="`${uploadPercentage}%`"></b-progress-bar>
                    </b-progress>
                    <span class="text-danger font-weight-bold" v-if="errMessage">Upload dibatalkan! ukuran file melebihi 60Mb</span>
                  </div>
                </div>
                <div v-else class="alert alert-custom alert-light-danger fade show mb-0 px-3 py-0" role="alert">
                  <div class="alert-icon"><i class="flaticon-warning"></i></div>
                  <div class="alert-text">
                    <b>Daftar Panduan tidak dapat diperbarui !</b> Data Input sudah berstatus <b>{{ activeTemplate.status_code_data_verification | parse('verify') }}</b>
                  </div>
                </div>
                <hr class="mb-0">
                <div class="d-flex pt-4 align-items-start justify-content-between">
                  <button v-if="!allowManipulateAttachment(activeTemplate)" @click="lockAttachment(activeTemplate, 'open')" class="btn btn-danger btn-sm"><i class="ri-lock-unlock-line pr-2"></i> Buka Template</button>
                  <button v-if="allowManipulateAttachment(activeTemplate)" @click="lockAttachment(activeTemplate, 'draft')" class="btn btn-success btn-sm"><i class="ri-lock-line pr-2"></i> Kunci Template</button>
                  <button v-if="allowManipulateAttachment(activeTemplate)" @click="deleteItems(activeTemplate)" class="btn btn-danger btn-sm"><i class="ri-delete-bin-2-line pr-2"></i> Hapus Item</button>
                </div>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
    <div v-else class="d-flex flex-column justify-content-center align-items-center">
      <img src="/static/img/filler/checklist.svg" alt="" srcset="">
      <div class="alert alert-custom alert-light-primary fade show mt-5" role="alert">
        <div class="alert-icon">
          <i class="flaticon-warning"></i>
        </div>
        <div class="alert-text"><b>Pilih Nama Divisi</b> terlebih dahulu untuk menampilkan data {{ config.title }}</div>
      </div>
    </div>
    <b-modal id="modal-custom-preview-doc" header-class="px-4 py-3" hide-footer title="Preview Dokumen" size="full">
      <template v-if="Object.keys(activeAttachment).length">
        <template v-if="showServer <= 1">
          <iframe :key="showServer + '-iframe'" class="animated" style="width:100%;height:700px;" :src="$_sys.iframeLink(activeAttachment.file_document.url, showServer)" frameborder="0"></iframe>
        </template>
        <template v-else>
          <embed class="animated" :src="activeAttachment.file_document.url" width="100%" height="700px" />
        </template>
      </template>
      <hr>
      <div class="d-flex align-items-center">
        <div>
          <button @click="showServer = 0" :class="showServer === 0 ? 'btn-primary' : 'btn-outline-primary'" class="btn">Server 1</button>
          <button @click="showServer = 1" :class="showServer === 1 ? 'btn-primary' : 'btn-outline-primary'" class="btn btn-primary">Server 2</button>
          <button @click="showServer = 2" :class="showServer === 2 ? 'btn-primary' : 'btn-outline-primary'" class="btn btn-primary">Server 3</button>
        </div>
        <div class="flex-fill pl-4">
          <div class="alert alert-custom alert-light-info fade show mb-0 px-3 py-0" role="alert">
            <div class="alert-icon"><i class="flaticon-warning"></i></div>
            <div class="alert-text"> Gunakan server lain atau <span class="font-weight-bolder">Download</span> apabila dokumen tidak dapat di buka </div>
          </div>
        </div>
      </div>
    </b-modal>
  </div>
</template>

<script>
export default {
  name: 'crud-monitoring',
  data () {
    return {
      config: {
        title: 'Template Data Input Kualitatif',
        permission: {
          create: 'create-template-qualitatif-on-divisions',
          read: 'create-template-qualitatif-on-divisions',
          // read: 'view-template-qualitatif-on-divisions',
          update: 'update-template-qualitatif-on-divisions',
          delete: 'delete-template-qualitatif-on-divisions'
        }
      },
      fullFilter: {},
      searchFilter: '',
      usedFilter: {
        search: '',
        page: 1,
        limit: 10
      },
      activeTemplate: {},
      activeAttachment: {},
      isLoading: false,
      dataList: {
        total: 0,
        totalPage: 1,
        data: []
      },
      newItem: '',
      newAgenda: '',
      uploadPercentage: 0,
      isUploading: false,
      showServer: 0,
      maxUploadSize: 60,
      errMessage: false,
      tempDate: null,
      cutOff: {},
      selectedAgenda: { label: 'RKAP Kualitatif', code: null },
      agendaList: []
    }
  },
  computed: {
    isValidFilter () {
      return !!(this.usedFilter.periode_year && this.usedFilter.division_id)
    }
  },
  watch: {
    searchFilter: {
      deep: false,
      immediate: false,
      handler (e) {
        this.usedFilter = Object.assign({}, this.usedFilter, {
          search: e,
          page: 1,
          limit: 10
        })
      }
    },
    selectedAgenda: {
      deep: true,
      immediate: false,
      handler (e) {
        const res = (typeof e === 'object') ? e.code : e
        this.usedFilter = Object.assign({}, this.usedFilter, {
          agenda_id: res,
          page: 1,
          limit: 10
        })
      }
    },
    usedFilter: {
      deep: true,
      immediate: false,
      handler (e) {
        this.getListData(e)
      }
    }
  },
  methods: {
    globalFilterChange (e) {
      this.fullFilter = Object.assign({}, e)
      this.getAgenda(e, null, true)
    },
    removeMyAction () {
      let query = Object.assign({}, this.$route.query)
      delete query.id
      this.$router.replace({ query })
    },
    getAgenda (e, selected = null, fromGlobal = false) {
      const tempFilter = Object.assign({}, {
        division_id: fromGlobal ? e.division_id && e.division_id.code ? e.division_id.code : null : e.division_id,
        periode_year: fromGlobal ? e.year : e.periode_year,
        agenda_id: selected || null
      })
      this.$_api.dataset('m-qualitatif-agendas', tempFilter).then(ag => {
        let agenda = ag.data.map(x => { return { label: x.name, code: x.id } })
        this.agendaList = [...[{ label: 'RKAP Kualitatif', code: null }], ...agenda]
        if (!selected) this.selectedAgenda = this.agendaList[0]
        else this.selectedAgenda = this.agendaList.filter(x => x.code === selected)[0]
        // ======
        this.usedFilter = Object.assign({}, tempFilter, {
          search: '',
          page: 1,
          limit: 10,
          id: this.$route.query.id || null
        })
        this.activeTemplate = Object.assign({}, {})
        this.activeAttachment = Object.assign({}, {})
        this.getCutOff(this.usedFilter)
      })
    },
    addNewAgenda (e) {
      this.isLoading = true
      const form = {
        division_id: this.usedFilter.division_id,
        periode_year: this.usedFilter.periode_year,
        name: this.newAgenda,
        active: 1
      }
      this.$_api.create('m-qualitatif-agendas', form).then(res => {
        this.isLoading = false
        this.newAgenda = ''
        this.$root.$emit('bv::hide::popover', 'add-newAgenda')
        this.getAgenda(this.usedFilter, res.data.id)
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    updateAgenda () {
      this.isLoading = true
      let form = {
        division_id: this.usedFilter.division_id,
        periode_year: this.usedFilter.periode_year,
        id: this.selectedAgenda.code,
        name: this.newAgenda
      }
      this.$_api.put('m-qualitatif-agendas' + '/update', form).then(res => {
        this.$_alert.success('Nama Agenda berhasil diperbarui')
        this.$root.$emit('bv::hide::popover', 'update-newAgenda')
        this.getAgenda(this.usedFilter, res.data.id)
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    deleteAgenda (e) {
      this.$_alert.confirm('Hapus Agenda ?', 'Semua panduan yang ada juga akan terhapus, lanjutkan ?').then((result) => {
        if (result.isConfirmed) {
          this.isLoading = true
          let form = {
            division_id: this.usedFilter.division_id,
            periode_year: this.usedFilter.periode_year,
            id: this.selectedAgenda.code
          }
          this.$_api.delete('m-qualitatif-agendas', { data: form }).then(async res => {
            this.isLoading = false
            this.getAgenda(this.usedFilter)
          }, err => {
            this.isLoading = false
            this.$_alert.error(err)
          })
        }
      })
    },
    getCutOff (e) {
      this.$_api.get('custom/cut-off-qualitatif-schedules/closing-date', e).then(res => {
        this.cutOff = res.data
      }, err => {
        this.$_alert.error(err)
      })
    },
    updateCutoff () {
      let form = {
        agenda_id: this.usedFilter.agenda_id,
        periode_year: this.usedFilter.periode_year,
        division_id: this.usedFilter.division_id,
        closing_date: this.cutOff.closing_date
      }
      this.$_api.put('custom/cut-off-qualitatif-schedules/update', form).then(res => {
        this.$_alert.success('Cut Off berhasil diperbarui')
      }, err => {
        this.$_alert.error(err)
      })
    },
    getListData (e, renewTemplate = false) {
      this.isLoading = true
      this.$_api.get('custom/template-qualitatif-on-divisions/list', e).then(res => {
        this.isLoading = false
        this.dataList = res
        if (renewTemplate) {
          const newVal = res.data.filter(x => x.id === this.activeTemplate.id)
          if (newVal.length) this.activeTemplate = Object.assign(newVal[0])
          else this.activeTemplate = Object.assign({}, {})
        } else {
          if (res.data.length) this.showTemplate(res.data[0])
          else this.activeTemplate = Object.assign({}, {})
        }
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    showTemplate (e) {
      this.activeTemplate = Object.assign(e)
    },
    showAtachment (e) {
      if (this.activeAttachment.id === e.id) this.activeAttachment = Object.assign({})
      else this.activeAttachment = Object.assign(e)
      this.$bvModal.show('modal-custom-preview-doc')
    },
    allowManipulateAttachment (e) {
      if (e.status_code_data_verification === 'open') return true
      // else if (e.status_code_data_verification === 'revision_request') return true
      // else if (e.status_code_data_verification === null) return true
      else return false
    },
    addNewItems (e) {
      this.isLoading = true
      const form = {
        agenda_id: this.usedFilter.agenda_id,
        division_id: this.usedFilter.division_id,
        periode_year: this.usedFilter.periode_year,
        template_name: this.newItem,
        active: 1
      }
      this.$_api.create('template-qualitatif-on-divisions', form).then(res => {
        this.isLoading = false
        this.newItem = ''
        this.$root.$emit('bv::hide::popover', 'add-newItem')
        this.getListData(this.usedFilter)
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    updateItems (e) {
      this.$_api.put('template-qualitatif-on-divisions' + '/update', e).then(res => {
        this.$_alert.success('Nama item Template berhasil diperbarui')
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    deleteItems (e) {
      this.$_alert.confirm('Hapus Item ?', 'Semua panduan yang ada juga akan terhapus, lanjutkan ?').then((result) => {
        if (result.isConfirmed) {
          this.$_api.delete('template-qualitatif-on-divisions', { data: e }).then(async res => {
            this.isLoading = false
            await this.getListData(this.usedFilter, false)
          }, err => {
            this.isLoading = false
            this.$_alert.error(err)
          })
        }
      })
    },
    handleFile (e) {
      if ((e.target.files[0].size / 1000000).toFixed(2) < this.maxUploadSize) {
        this.errMessage = false
        this.isUploading = true
        let file = e.target.files[0]
        this.$_api.fileUpload(file, event => {
          this.uploadPercentage = Math.round((100 * event.loaded) / event.total)
        }).then(res => {
          this.isUploading = false
          this.addNewAttachment(res.data)
          e.target.value = null
        }).catch((err) => {
          this.isUploading = false
          this.$_alert.error(err)
        })
      } else {
        this.errMessage = true
        e.target.value = null
      }
    },
    addNewAttachment (e) {
      this.isLoading = true
      const form = {
        template_qualitatif_on_division_id: this.activeTemplate.id,
        file_document: e
      }
      this.$_api.create('template-qualitatif-on-division-attachments', form).then(async res => {
        this.isLoading = false
        await this.getListData(this.usedFilter, true)
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    deleteAttachment (e) {
      this.$_alert.confirm('Hapus Panduan ?', 'Data yang sudah dihapus tidak dapat dikembalikan, lanjutkan ?').then((result) => {
        if (result.isConfirmed) {
          this.$_api.delete('template-qualitatif-on-division-attachments', { data: e }).then(async res => {
            this.isLoading = false
            await this.getListData(this.usedFilter, true)
          }, err => {
            this.isLoading = false
            this.$_alert.error(err)
          })
        }
      })
    },
    lockAttachment (data, e) {
      const text = e !== 'draft' ? `<p class="my-6"> Tindakan ini tidak bisa dibatalkan. Ini akan <b>menghapus secara permanen</b> semua verifikasi yang telah dilakukan pada dokumen ini.</p>
        <span class="text-dark-75">Silahkan ketik <span class="text-dark-75 font-weight-bolder">"konfirmasi"</span> untuk membuka dokumen ini</span>`
        : `<p class="my-6"> Setelah anda melakukan kunci template, maka <b>template dapat diisi oleh inputer divisi</b> untuk pengumpulan data.</p>
        <span class="text-dark-75">Silahkan ketik <span class="text-dark-75 font-weight-bolder">"konfirmasi"</span> untuk membuka dokumen ini</span>`
      this.$swal({
        title: e === 'draft' ? 'Kunci Template ?' : 'Buka Kunci Template',
        html: `
        <div class="alert alert-custom alert-outline-danger mt-3 p-3" role = "alert">
            <div class="alert-icon">
              <span class="svg-icon svg-icon-danger svg-icon-xl">
                <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                  <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                      <polygon points="0 0 24 0 24 24 0 24"/>
                      <path d="M5.85714286,2 L13.7364114,2 C14.0910962,2 14.4343066,2.12568431 14.7051108,2.35473959 L19.4686994,6.3839416 C19.8056532,6.66894833 20,7.08787823 20,7.52920201 L20,20.0833333 C20,21.8738751 19.9795521,22 18.1428571,22 L5.85714286,22 C4.02044787,22 4,21.8738751 4,20.0833333 L4,3.91666667 C4,2.12612489 4.02044787,2 5.85714286,2 Z" fill="#000000" fill-rule="nonzero" opacity="0.3"/>
                      <path d="M14.5,12 C15.0522847,12 15.5,12.4477153 15.5,13 L15.5,16 C15.5,16.5522847 15.0522847,17 14.5,17 L9.5,17 C8.94771525,17 8.5,16.5522847 8.5,16 L8.5,13 C8.5,12.4477153 8.94771525,12 9.5,12 L9.5,11.5 C9.5,10.1192881 10.6192881,9 12,9 C13.3807119,9 14.5,10.1192881 14.5,11.5 L14.5,12 Z M12,10 C11.1715729,10 10.5,10.6715729 10.5,11.5 L10.5,12 L13.5,12 L13.5,11.5 C13.5,10.6715729 12.8284271,10 12,10 Z" fill="#000000"/>
                  </g>
                </svg>
              </span>
            </div>
            <div class="alert-text">Perhatikan perintah sebelum melanjutkan!</div>
        </div>` + text,
        input: 'text',
        icon: 'warning',
        iconColor: '#d42b21',
        padding: '.75rem',
        width: '37rem',
        focusConfirm: false,
        showCancelButton: false,
        confirmButtonText: e === 'draft' ? 'Saya paham konsekuensinya, Kunci template ini' : 'Saya paham konsekuensinya, Buka Kunci template ini',
        customClass: {
          confirmButton: 'btn btn-light-danger',
          actions: 'mt-0 mb-5',
          htmlContainer: 'text-left overflow-hidden'
        },
        didOpen: () => {
          this.$swal.disableButtons()
          this.$swal.getInput().addEventListener('keyup', (e) => {
            if (e.target.value === 'konfirmasi') {
              this.$swal.enableButtons()
            } else {
              this.$swal.disableButtons()
            }
          })
        }
      }).then((result) => {
        if (result.isConfirmed) this.sendLockAttachment(data, e)
      })
    },
    sendLockAttachment (data, e) {
      let form = { ...this.usedFilter, ...{ id: data.id, status_code: e } }
      this.$_api.post('custom/template-qualitatif-on-divisions/do-lock-unlock', form).then(async res => {
        this.isLoading = false
        await this.getListData(this.usedFilter, true)
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    }
  }
}
</script>

<style>
  .popover-xl {
    max-width: 400px !important;
    width: 400px !important;
  }
  .rotate-90 {
    transform: rotate(90deg);
  }
</style>



